                <v-card v-else-if="selectedTab == 0">
                    <v-row>
                        <v-col v-for="(clazz, index) in clazzList" :key="index" md="3">
                            <v-card outlined
                                    class="mx-auto"
                                    style="width: 400px; height: 480px; justify-content: center">
                                <!--                                <v-btn @click="thumbnailImg(clazz)"></v-btn>-->
                                <v-img :aspect-ratio="16/9"
                                       max-width="400px"
                                       max-height="200px"
                                       :src="clazz.thumbnail"
                                ></v-img>
                                <!--                                <v-img-->
                                <!--                                        :src="projectInfomation['img']"-->
                                <!--                                        max-width="400"-->
                                <!--                                        max-height="300"-->
                                <!--                                >-->
                                <v-card-title style="height:120px">
                                    <!--                        {{course}}-->
                                    {{clazz.className}}
                                </v-card-title>
                                <v-card-text style="height:108px">
                                    코스명 : {{clazz.course ? clazz.course.courseDesc : clazz.courseId}} <br>
                                    클래스 시작 : {{clazz.classStartDate}} <br>
                                    클래스 종료 : {{clazz.classEndDate}} <br>
                                </v-card-text>
                                <v-card-actions>
                                    <v-spacer></v-spacer>
                                    <v-btn text
                                           @click="joinClass(clazz)">
                                        입장
                                    </v-btn>
