<script>
    var Minio = require('minio');

    export default {
        name: "ClazzListPage",
        components: {},
        data: () => ({
            clazzList: [],
            teacherClassList: [],
            overlay: false,
            selectedTab: 0,
            email: '',
            loaded: false,
            labsTabLists: [{tabName: '수강중인 클래스', key: 'study'}, {tabName: '강의중인 클래스', key: 'lecture'}],
            tempSearch: ''
        }),
        computed: {
            // loadingStatus() {
            //     if (Object.keys(this.clazzList).length < 1) {
            //         return true
            //     } else {
            //         return false
            //     }
            // },
            sortedClazzList() {
                var me = this
                var o = me.clazzList;
                var sorted = {},
                    key, a = [];
                // 키이름을 추출하여 배열에 집어넣음
                for (key in o) {
                    if (o.hasOwnProperty(key)) a.push(key);
                }
                // 키이름 배열을 정렬
                a.sort();
                // 정렬된 키이름 배열을 이용하여 object 재구성
                for (key = 0; key < a.length; key++) {
                    sorted[a[key]] = o[a[key]];
                }
                return sorted;
            }
        },
        watch: {},
        mounted() {
            var me = this
            this.email = localStorage.getItem("email");
            if (this.email) {
                this.getClazzList();

            }
        },
        methods: {
            toDataURL(url, callback) {
                var xhr = new XMLHttpRequest();
                xhr.onload = function () {
                    var reader = new FileReader();
                    reader.onloadend = function () {
                        callback(reader.result);
                    }
                    reader.readAsDataURL(xhr.response);
                };
                xhr.open('GET', url);
                xhr.responseType = 'blob';
                xhr.send();
            },
            async thumbnailImg(clazz) {
                var me = this
                var checkImg = await this.checkExistImg(clazz)
                if (!checkImg) {
                    // 체크 이미지가 없을땐 기본 이미지를 뿌려줌
                    var path = clazz.courseId + "/default.png";

                    var img = await this.getThumbnailImg(path)
                    console.log(img)
                    return img
                }
                var img = await this.getThumbnailImg(checkImg)
                console.log(img)
                return img
                // 체크 이미지가 있을때 타는 곳
            },
            getThumbnailImg(path) {
                return new Promise((resolve, reject) => {
                    this.$minioClient.getObject('labs', path, function (err, imgDataStream) {
                        if (err) {
                            reject(err);
                            return;
                        }
                        if (!imgDataStream) {
                            reject(new Error("no stream connected"));
                            return;
                        }
                        imgDataStream.on('data', function (imgChunk) {
                            var base64Data = btoa(String.fromCharCode.apply(null, imgChunk))
                            resolve('data:image/png;base64,' + base64Data)
                        })

                        imgDataStream.on('error', function (err) {
                            reject(err);
                        })
                    })
                });

            },
            checkExistImg(clazz) {
                var listObj = [];
                var checkImg = false
                var imgPath = ''
                var path = clazz.courseId + "/runningClass/" + clazz.classId + '/';
                return new Promise((resolve, reject) => {
                    this.$minioClient.listObjects('labs', path, false)
                        .on('data', function (obj) {
                            if (obj.name) listObj.push(obj.name)
                        })
                        .on('error', function (err) {
                            reject(err);
                        })
                        .on('end', function (err) {
                            listObj.forEach(function (item) {
                                imgPath = item
                                if (item.includes('jpg') || item.includes('png')) checkImg = true
                            });
                            if (checkImg)
                                resolve(imgPath);
                            else
                                resolve(false)
                        })
                });
            },
            joinClass(clazz) {
                var me = this
                console.log(localStorage.getItem('picture'))
                var userId = localStorage.getItem('email')

                me.$minioClient.putObject('labs', `${clazz.courseId}/runningClass/${clazz.classId}/userImage/${userId}.txt`, localStorage.getItem('picture'), function (err, etag) {
                })
                this.$router.push('/courses/' + clazz.courseId + '/' + clazz.classId)
            },
            async getClazzList() {
                var me = this

                await new Promise((resolve, reject) => {
                    this.$minioClient.listObjects('labs', '', true)
                        .on('data', function (obj) {
                            if (obj.name && obj.name.includes('Class_Metadata') && obj.size != 0) {
                                me.$minioClient.getObject('labs', obj.name, function (err, dataStream) {
                                    if (err) {
                                        return console.log(err)
                                    }
                                    dataStream.on('data', function (chunk) {
                                        var string = new TextDecoder("utf-8").decode(chunk);
                                        var classMetadata = JSON.parse(string)
                                        // json["path"] = obj.name.replace('Class_Metadata.json', '')
                                        // var thumbnailURL = obj.name.replace('Class_Metadata.json', 'thumbnail.png')
                                        if (classMetadata.teacherId == me.email) {
                                            // 강사
                                            me.teacherClassList.push(classMetadata)
                                        }
                                        // filtering
                                        classMetadata.groupedUsers.some(item => {
                                            return item.users.some(user => {
                                                    if (user.email == me.email) {

                                                        // get course info
                                                        var nameParts = obj.name.split("/");
                                                        console.log(nameParts)
                                                        classMetadata.classId = nameParts[2];
                                                        classMetadata.courseId = nameParts[0];

                                                        me.$minioClient.getObject('labs', classMetadata.courseId + "/Course_Metadata.json", function (err, dataStream) {
                                                            if (err) {
                                                                return console.log(err)
                                                            }
                                                            dataStream.on('data', async function (chunk) {
                                                                var string = new TextDecoder("utf-8").decode(chunk);
                                                                var courseMetadata = JSON.parse(string)
                                                                classMetadata.course = courseMetadata;
                                                                classMetadata.thumbnail = await me.thumbnailImg(classMetadata)
                                                                //notify manually
                                                                classMetadata.__ob__.dep.notify()
                                                            })

                                                            dataStream.on('error', function (err) {
                                                                console.log(err)
                                                            })
                                                        })

                                                        me.clazzList.push(classMetadata)

                                                        return true;

                                                    }
                                                }
                                            )
                                        })


                                        // me.$minioClient.getObject('labs', thumbnailURL, function (err, dataStream) {
                                        //     if (err) {
                                        //         return console.log(err)
                                        //     }
                                        //     dataStream.on('data', function (chunk) {
                                        //         var base64Data = btoa(String.fromCharCode.apply(null, chunk))
                                        //         json['imgSrc'] = 'data:image/png;base64,' + base64Data
                                        //         json['course'] = obj.name.split('/')[0];
                                        //         me.$set(me.clazzList, obj.name.split('/')[0] + '/' + obj.name.split('/')[2], json)
                                        //     })
                                        //     dataStream.on('error', function (err) {
                                        //         console.log(err)
                                        //     })
                                        // })
                                    })
                                    dataStream.on('error', function (err) {
                                        console.log(err)
                                    })
                                })
                            }
                        })
                        .on('error', function (err) {
                            alert(err);
                            reject();
                        })
                        .on('end', function (err) {
                            resolve();
                        })

                });

                me.loaded = true;


            },
        }
    };
</script>
