<template>
    <v-container fluid style="height: 100%">
        <v-row no-gutters v-if="labInfo" style="height: 100%">
            <v-navigation-drawer
                    v-model="guideOpened"
                    clipped
                    app
                    width="400"
            >
                <div align="right" style="margin-top: 5px; margin-right: 5px; margin-bottom: 5px">
                    <v-icon text @click="closeGuide()">mdi-close</v-icon>
                </div>

                <Countdown :deadline="deadline"></Countdown>

                <v-list dense>
                    <v-subheader>
                        Instruction
                    </v-subheader>

                    <v-col>
                        <v-sheet v-html="labInfo.instructionHtml"></v-sheet>
                    </v-col>


                    <v-divider></v-divider>

                    <v-subheader>
                        Checkpoints
                    </v-subheader>
                    <v-list-item v-for="(item,idx) in labInfo.checkPoints" :key="idx">
                        <v-list-item-content>
                            <v-list-item-title>{{idx + 1}}. {{item.text}}</v-list-item-title>
                        </v-list-item-content>
                        <v-list-item-action>
                            <v-checkbox
                                    v-model="item.status"
                                    success
                                    :readonly="!isAdmin"
                            ></v-checkbox>
                        </v-list-item-action>
                    </v-list-item>
                </v-list>
                <v-divider></v-divider>

                <v-col class="text-center" cols="12" sm="4" v-if="isAdmin || myId==userId">
                    <v-btn @click="submitResultByUser()" large color="primary">제출</v-btn>
                </v-col>
                <v-col class="text-center" cols="12" sm="4">
                    <v-btn :to="'/courses/' + courseId + '/' + classId + '/' + labId + '/class-room'" text>클래스룸으로 이동</v-btn>
                </v-col>

            </v-navigation-drawer>
            <component ref="toolComp"
                    :is="'lab-tool-' + labInfo.tool"
                    v-model="tool"
                    :lab-info="labInfo"
                    @change="onResultChanged"
                    style="height: 100% !important; width: 100%">
            </component>
        </v-row>
        <v-row v-else>
            <v-progress-circular
                    :size="50"
                    color="primary"
                    indeterminate
            ></v-progress-circular>
        </v-row>
    </v-container>
</template>

<script>
    import LabBase from "./LabBase"
    import LabToolKuberEz from "./tools/LabToolKuberEz"
    import LabToolEventStorming from "./tools/LabToolEventStorming"
    import LabToolIde from "./tools/LabToolIDE"
    import Countdown from 'vuejs-countdown'

    var jp = require('jsonpath');

    export default {
        name: "LabLocator",
        components: {
            LabToolEventStorming,
            LabToolKuberEz,
            LabToolIde,
            Countdown
        },
        mixins: [LabBase],
        props: {},
        data: () => ({
            labInfo: null,
            overlay: false,
            tool: {result: null},
            guideOpened: true,
            autoSubmit: '',
            deadline: null
        }),
        computed: {
            userId() {
                if (this.$route.params.userId)
                    return this.$route.params.userId;

                return this.myId;
            }
        },
        // watch: {
        //     "tool.result": {
        //         immediate: true,
        //         deep: true,
        //         handler(newVal) {
        //             if (newVal != null) {
        //                 this.testCheckpoints(newVal);
        //             }
        //         }
        //     }
        // },
        beforeDestroy() {
            clearInterval(this.autoSubmit)
        },
        async mounted() {
            var me = this

            await this.loadLabInfo();

            this.autoSubmit = setInterval(function ()  {
                me.submitResult();
            },60000)

        },
        methods: {
            async loadCheckPoints() {
                var me = this;
                console.log("in checkPoint")
                var userResult = await this.getObject(
                    `${this.courseId}/runningClass/${this.classId}/labs/${this.labId}/${me.userId}/checkpointResults.json`
                );
                if(userResult != null) {
                    userResult.forEach(function (item, idx) {
                        if (!item.regExp && !item.javascript) {
                            me.labInfo.checkPoints[idx] = item
                        }
                    })
                }
            },
            onResultChanged(result) {
                this.testCheckpoints(result)
            },
            async loadLabInfo() {
                this.labInfo = await this.getLabInfo();
                console.log("in labinfo")
                var me = this;
                var storedResult = await this.getObject(`${me.courseId}/runningClass/${me.classId}/labs/${me.labId}/${me.userId}/toolResult.json`)

                if (storedResult) //important!
                    this.tool.result = storedResult;
                else {
                    var startingArtifact = await this.getObject(`${me.courseId}/planed/${me.labId}/${me.labInfo.startArtifact}`)
                    if (startingArtifact) //important!
                        this.tool.result = startingArtifact;
                }
                this.tool.__ob__.dep.notify();
                await this.loadCheckPoints();

                this.getLabStatus(this.labId).then(labStatus=>{
                    me.deadline = labStatus.startedAt// + me.labInfo.labTime * 60
                });

            },
            testCheckpoints(result) {
                var me = this
                this.labInfo.checkPoints.forEach(async function (checkpoint, idx) {
                    var result = me.tool.result;
                    var v = me.tool;
                    var t = me.$refs["toolComp"];
                    var passed = false;
                    if (checkpoint.javascript) {
                        if (checkpoint.javascript.endsWith('.js')) {
                            var jsPath = `${me.$route.params.courseId}/planed/${me.$route.params.labId}/${checkpoint.javascript}`
                            checkpoint.javascript = await me.getString(jsPath);
                        }

                        var everSet;
                        function returnValue(value) {
                            passed = value
                            everSet = true;
                        }

                        function containsAll(arr1, arr2) {
                            for(var i=0; i<arr2.length; i++){
                                if(!arr1.includes(arr2[i]))
                                    return false

                            }

                            return true;
                        }
                        function oneOf(arr1, arr2) {
                            for(var i=0; i<arr2.length; i++){
                                if(arr1.includes(arr2[i]))
                                    return true

                            }

                            return false;
                        }
                        var javascript = checkpoint.javascript;
                        try{
                            var passedReturn = eval(javascript);
                            if(!everSet)
                                passed = passedReturn;
                        }catch(e){
                            console.error(e)
                        }

                    } else if (checkpoint.regExp) {
                        var expression = new RegExp(checkpoint.regExp);
                        passed = expression.test(me.tool.result);
                    } else if (checkpoint.testShell) {
                        var testKey = checkpoint.testShell;

                        passed = me.testCheckpointWithShellInlineTest(result, testKey)

                    }

                    checkpoint.status = passed
                    checkpoint.__ob__.dep.notify()
                })

            },

            testCheckpointWithShellInlineTest(result, testKey) {
                const prefix = "####### test "; // 7-sharp test

                var lines = result.split("\n");
                var answerPart = null;
                var testKeyPart = null;

                for (var i = 0; i < lines.length; i++) {
                    var line = lines[i];
                    if (line.startsWith(prefix)) {

                        var testKeyAndRegExp = line.split(":")
                        testKeyPart = "test " + testKeyAndRegExp[0].substring(prefix.length, testKeyAndRegExp[0].length).trim();
                        answerPart = testKeyAndRegExp[1].trim();

                        if(answerPart.includes(',')){
                            answerPart = answerPart.split(',').map(eachAnswer=>{return eachAnswer.trim()})
                        }

                        continue;
                    }

                    if (testKeyPart == testKey && answerPart){
                        if(line.includes(answerPart)) {
                            return true;
                            answerPart = null;
                        }

                        if(answerPart.forEach){
                            answerPart.forEach(eachAnswer=>{
                                if(line.includes(eachAnswer)){
                                    answerPart.splice(answerPart.indexOf(eachAnswer),1)
                                }
                            })

                            if(answerPart.length == 0){
                                return true;
                            }
                        }
                    }
                }

                return false;
            },
            closeGuide() {
                // 창닫기
                var me = this
                this.guideOpened = false
                this.$EventBus.$emit('guideClose')
                this.$EventBus.$on('guideOpen', function () {
                    me.guideOpened = true
                })
            },
            async submitResultByUser() {
                clearInterval(this.autoSubmit);
                await this.submitResult();
                alert('제출하였습니다');
            },
            async submitResult() {
                var me = this;

                var promises = [];

                promises.push(
                    this.putObject(`${me.courseId}/runningClass/${me.classId}/labs/${me.labId}/${me.userId}/checkpointResults.json`, me.labInfo.checkPoints)
                );
                promises.push(
                    this.putObject(`${me.courseId}/runningClass/${me.classId}/labs/${me.labId}/${me.userId}/toolResult.json`, this.tool.result)
                );

                return Promise.all(promises);
            },

        },


    };
</script>
